import { Component, OnInit, ElementRef, Renderer2 } from '@angular/core';
import { MessageService } from '../message.service';
import { ToolbarService } from '../toolbar.service';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {
  constructor(private toolbarService: ToolbarService, private messageService: MessageService,
    private element: ElementRef, private renderer: Renderer2) { }

  ngOnInit() {
    this.messageService.listen('chat message').subscribe(data => {
      const t = this.renderer.createText(JSON.stringify(data));
      const p = this.renderer.createElement('p');
      this.renderer.appendChild(p, t);
      this.renderer.appendChild(this.element.nativeElement, p);
      this.element.nativeElement.scrollIntoView(false);
    });

  }

}



