import { Injectable } from '@angular/core';
import * as SocketIO from 'socket.io-client';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/share';

@Injectable()
export class MessageService {
  private DEBUG = true;
  socket: SocketIOClient.Socket;
  socketConnected$ = new BehaviorSubject<boolean>(false); // $ => Observable
  listenedEventStore = {};

  constructor() {
    this.socket = SocketIO('renel972.herokuapp.com');
    this.socket.on('connect', () => this.socketConnected$.next(true));
    this.socket.on('disconnect', () => this.socketConnected$.next(false));

    this.socketConnected$.asObservable().subscribe(connected => {
      console.log('Socket connected: ', connected);
    });


    if (this.DEBUG) {
      this.listen('chat message').subscribe(data => {
        console.log(data);
      });
    }
  }

  get id() {
    return this.socket.id;
  }

  send(event: string, message: string) {
    this.socket.emit(event, this.newMessage(message));
  }

  newMessage(message: string) {
    return new Message(this.id, Date.now(), message);
  }

  listen(event: string): Observable<any> {
    this.listenedEventStore[event] = new Observable(observer => {

      this.socket.on(event, data => {
        observer.next(data);
      });

      // observable is disposed
      return () => {
        this.socket.off(event);
      };

    }).share();

    return this.listenedEventStore[event];

  }

}

export class Message {
  source: string;
  date: number;
  message: string;

  constructor(source: string, date: number, message: string) {
    this.source = source;
    this.date = date;
    this.message = message;
  }
}



